* {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  font-weight: normal;
  text-decoration: none;
  text-rendering: optimizeLegibility;
  box-sizing: border-box;
}

.bigtext {
  font-family: 'Lobster', 'Roboto', sans-serif;
  font-size: 50pt;
  color: black;
  word-wrap: normal;
  text-align: center;
}

.header {
  font-family: 'Poppins', 'Roboto', sans-serif;
  font-size: 30pt;
  color: white;
  word-wrap: normal;
  text-align: center;
}

header {
  font-family: 'Poppins', 'Roboto', sans-serif;
  font-size: 30pt;
  color: white;
  word-wrap: normal;
  text-align: center;

  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #449edad2;
}

body {
  background-color: rgb(38, 38, 38);
  overflow: hidden;
}

.button {
  color: black;
  margin: 0 5px;
  padding: 5px 15px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  border-radius: 5px;

  transition-property: background-color, padding;
  transition-timing-function: ease-in-out;
  transition-duration: 0.3s;
}

.button:hover {
  background-color: #ffcb7d88;
  padding: 5px 25px;
}

.hirebtn {
  background-color: #ffae62;
}
.hirebtn:hover {
  background-color:#ffcb7d;
}


@media only screen and (max-width: 550px) {
  .bigtext {
    font-size: 35pt;
  }
  .header {
    font-size: 25pt;
  }
}
