nav {
    position: sticky;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;

    width: 100%;
    height: 45px;
    padding: 5px 15px;

    backdrop-filter: blur(10px);
    background-color: rgba(255, 255, 255, 0.2);
    z-index: 1;
}

nav * {
    font-family: 'Poppins', 'Roboto', sans-serif;
    font-size: 14pt;
}

nav .logo {
    margin: 0 10px;
    align-self: flex-start;
    padding: 0;
    height: 55px;
    border-radius: 50%;
}

nav .logocont {
    align-self: flex-start;
    display: flex;
}

nav .buttons {
    display: flex;
    justify-content: space-evenly;
    min-width: 460px;
    margin-right: 10px;
}

nav .about {
    transform: scale(1.5);
    margin-right: 8px;
}
nav .projects {
    transform: scale(1.15) translateY(-1px);
    margin-right: 5px;
}

nav h1 {
    display: block;
    color: black;
    align-self: flex-start;
    margin-top: 2px;
    font-size: 16pt;
}

nav .lang {
    font-weight: 600;
    font-size: 12pt;
    align-self: center;
}

.darkmode * {
    color: white;
    fill: white;
}