.loadingpage {
    width: 100%;
    min-height: 100vh;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    position: absolute;
    top: 0;
    left: 0;
}

.backdrop {
    height: 25vh;
    border-radius: 50%;

    animation: jump 1s ease-in-out infinite;
}

@keyframes jump {
    0% {
        scale: .9;
    }
    30% {
        scale: 1;
    }
    100% {
        scale: .9;
    }
}