.focus-enter {
    transform: scale(1.2);
    opacity: 0;
}

.focus-enter-done {
    transform: scale(1);
    opacity: 100%;
}

.focus-exit {
    transform: scale(1);
    opacity: 100%;
}

.focus-exit-active {
    transform: scale(.8);
    opacity: 0;
}

.focus-enter-done, .focus-exit-active {
    transition-property: transform opacity;
    transition-duration: 0.5s;
    transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
}